.ProgressBarStyles-module__progressBar_BmcOnG__ {
  --color1: #049fd9;
  --color7: #e8ebf1;
  --height: 6px;
  --small2: .625rem;
  appearance: none;
  height: var(--height);
  margin-bottom: var(--small2);
  border: 1px solid #fff;
  border-radius: 15px;
  width: 100%;
}

.ProgressBarStyles-module__progressBar_BmcOnG__[value] {
  background: var(--color7);
  border-radius: var(--height);
}

.ProgressBarStyles-module__progressBar_BmcOnG__::-webkit-progress-bar {
  border-radius: var(--height);
  background-color: var(--color7);
}

.ProgressBarStyles-module__progressBar_BmcOnG__::-webkit-progress-value {
  background: var(--color1);
  border-radius: var(--height);
}

.ProgressBarStyles-module__progressBar_BmcOnG__[value]::-moz-progress-bar {
  border-radius: var(--height);
  background-color: var(--color1);
  margin: 0 auto;
}
