.mds-rebuild-select {
  --mds-select-border-color: var(--control-border);
  --mds-select-background-color: var(--control-bg-weak);
  --mds-select-icon-color: var(--interact-icon-weak);
  --mds-select-animation: all 0.3s cubic-bezier(0.7, 0.3, 0.1, 1);
  --mds-select-padding: 6px 10px;
  --mds-select-height: 34px;
  --mds-select-font: var(--typography-p3-regular);
  --mds-select-color: var(--control-border);
  --mds-select-gap: 8px;

  cursor: pointer;

  position: relative;

  display: flex;
  gap: var(--mds-select-gap);
  align-items: center;

  box-sizing: border-box;
  width: stretch;
  height: var(--mds-select-height);
  max-height: var(--mds-select-height);
  padding: var(--mds-select-padding);

  background-color: var(--mds-select-background-color);
  border: 2px var(--mds-select-border-color) solid;
  border-radius: 6px;
  box-shadow: 0 0 0 0 var(--color-transparent);

  transition: var(--mds-select-animation);

  & .mds-rebuild-select-caret {
    transform-origin: 50% 50%;
    transform-style: preserve-3d;

    width: 12px;
    height: 12px;

    color: var(--interact-icon-weak);

    transition: var(--mds-select-animation);

    & path {
      stroke: var(--interact-icon-weak);
      stroke-width: 10;
    }
  }

  &.mds-rebuild-select-is-opened {
    --mds-select-border-color: var(--control-border-active);

    box-shadow: 0 0 0 2px #d8e6fa;

    & svg.mds-rebuild-select-caret {
      transform: rotate(-180deg);
    }
  }

  &:hover,
  &:active {
    --mds-select-border-color: var(--control-border-hover);

    outline: 0;
  }

  & .mds-rebuild-select-placeholder,
  & .mds-rebuild-select-selected-value {
    overflow: hidden;
    flex: 100;

    max-width: 100%;

    font: var(--mds-select-font);
    color: var(--mds-select-color);
    text-align: left;
    text-overflow: ellipsis;
    white-space: nowrap;
  }

  & .mds-rebuild-select-selected-value {
    color: var(--color-font-strong);
  }

  &:disabled {
    --mds-select-border-color: var(--control-border-disabled);
    --mds-select-background-color: var(--control-bg-weak-disabled);
    --mds-select-color: var(--default-text-disabled);
    --mds-select-icon-color: var(--interact-icon-weak-disabled);

    cursor: not-allowed;

    & .mds-rebuild-select-placeholder,
    & .mds-rebuild-select-selected-value {
      color: var(--mds-select-color);
    }
  }

  &.mds-rebuild-select-is-small {
    --mds-select-padding: 3px 6px;
    --mds-select-height: 28px;
    --mds-select-font: var(--typography-p4-regular);
    --mds-select-gap: 4px;
  }
}

.mds-rebuild-select-menu {
  --mds-select-menu-font: var(--typography-p3-regular);
  --mds-select-menu-bg-color: var(--control-bg-weak);
  --mds-select-icon-color: var(--interact-icon-weak);
  --mds-select-icon-height: 20px;
  --mds-select-icon-width: 20px;
  --mds-select-menu-padding: 6px 10px;
  --mds-select-divider: 1px var(--default-border) solid;
  --mds-select-gap: 8px;

  overflow-x: hidden;
  overflow-y: auto;

  box-sizing: border-box;
  max-height: inherit;
  padding: 2px 0;

  background-color: var(--mds-select-menu-bg-color);
  border: 2px var(--control-border) solid;
  border-radius: 6px;
  outline: 0;
  box-shadow: var(--shadow-subtle);

  & svg {
    width: var(--mds-select-icon-width);
    height: var(--mds-select-icon-height);
  }

  & ul {
    margin: 0;
    padding: 0;
    list-style-type: none;

    & li.mds-rebuild-select-menu-item {
      --mds-select-menu-text-color: var(--default-text);
      --mds-select-menu-item-font: var(--mds-select-font);

      cursor: pointer;

      display: flex;
      gap: var(--mds-select-gap);
      align-items: center;

      margin: 0;
      padding: var(--mds-select-menu-padding);

      font: var(--mds-select-menu-font);
      color: var(--mds-select-menu-text-color);
      text-align: left;

      background-color: var(--mds-select-menu-bg-color);
      border: none;
      border-radius: 0;

      transition: all 0.1s cubic-bezier(0.7, 0.3, 0.1, 1);

      & svg.mds-rebuild-select-check {
        & polyline {
          stroke-width: 35;
        }
      }

      & .mds-rebuild-select-menu-item-label {
        width: 100%;
      }

      &.mds-rebuild-select-is-active,
      &:hover,
      &:active,
      &:focus {
        --mds-select-icon-color: var(--interact-icon-weak-active);

        background-color: var(--control-bg-weak-hover);
      }

      &.mds-rebuild-select-is-selected {
        --mds-select-icon-color: var(--interact-icon-weak-active);

        background-color: var(--control-bg-weak-active);
      }
    }
  }
}
