.mds-text {
  margin: 0;
  padding: 0;

  /* TODO: Consider replacing this once we have all the font variables defined. */
  font-family: var(--typography-font-body);
  font-variant-numeric: tabular-nums;
}

code.mds-text {
  display: inline-block;
  font-family: "Roboto Mono", SFMono-Regular, Consolas, "Liberation Mono", Menlo,
    Courier, monospace;
  font-variant-numeric: tabular-nums;
  background-color: var(--default-text-medium);
}

.mds-text-monospace {
  font-family: "Roboto Mono", SFMono-Regular, Consolas, "Liberation Mono", Menlo,
    Courier, monospace;
}

.mds-text-p1 {
  font-size: 18px;
  line-height: 24px;
}

.mds-text-p2 {
  font-size: 16px;
  line-height: 22px;
}

.mds-text-p3 {
  font-size: 14px;
  line-height: 20px;
}

.mds-text-p4 {
  font-size: 12px;
  line-height: 18px;
}

.mds-text-weight-regular {
  font-weight: 400;
}

.mds-text-weight-semi-bold {
  font-weight: 600;
}

.mds-text-weight-bold {
  font-weight: 700;
}

.mds-text-ellipsis {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.mds-text-compact {
  margin-bottom: 0;
  padding-bottom: 0;
}

.mds-text-color-regular {
  color: var(--default-text);
}

.mds-text-color-light {
  color: var(--default-text-medium);
}
