.mds-notification {
  --mds-notification-bg-color: transparent;
  --mds-notification-border-color: transparent;

  display: flex;
  gap: 8px;
  align-items: flex-start;

  padding: 12px 16px;

  background-color: var(--mds-notification-bg-color);
  border: 1px solid transparent;
  border-radius: var(--size-radius-border-sm);

  &:not(.mds-notification-banner) {
    &:not(.mds-notification-admin) {
      &.mds-notification-informational {
        --mds-notification-bg-color: var(--info-bg-strong);
      }

      &.mds-notification-warning {
        --mds-notification-bg-color: var(--warning-bg-strong);
      }

      &.mds-notification-negative {
        --mds-notification-bg-color: var(--negative-bg-strong);
      }

      &.mds-notification-positive {
        --mds-notification-bg-color: var(--positive-bg-strong);
      }
    }

    &.mds-notification-admin {
      background-color: var(--color-purple-1);
      border: 1px dashed var(--color-purple-6);
    }
  }

  &.mds-notification-banner {
    gap: 18px;

    padding: 16px 20px 16px 12px;

    border-left: 6px solid var(--mds-notification-border-color);
    border-radius: 6px;
    box-shadow: var(--shadow-contain-medium);

    &:not(.mds-notification-admin) {
      &.mds-notification-informational {
        --mds-notification-border-color: var(--info-border);
        --mds-notification-bg-color: var(--info-bg-weak);
      }

      &.mds-notification-negative {
        --mds-notification-border-color: var(--negative-border);
        --mds-notification-bg-color: var(--negative-bg-weak);
      }

      &.mds-notification-warning {
        --mds-notification-border-color: var(--warning-border);
        --mds-notification-bg-color: var(--warning-bg-weak);
      }

      &.mds-notification-positive {
        --mds-notification-border-color: var(--positive-border);
        --mds-notification-bg-color: var(--positive-bg-weak);
      }
    }

    &.mds-notification-admin {
      background-color: var(--color-purple-1);
      border: 1px dashed var(--color-purple-6);
      border-left: 6px solid var(--color-purple-6);
    }
  }

  & .mds-notification-content {
    flex: 1;
    align-self: center;
  }

  & .mds-notification-close-button {
    cursor: pointer;

    display: flex;
    align-items: center;

    padding: var(--size-2xs);

    background: none;
    border: 0;
  }
}
